import { Router } from 'express'
import { Op, QueryTypes } from 'sequelize'
import sequelize from '../models/sequelize'
import toRes from '../lib/toRes'
import JiuyexinxiModel from '../models/JiuyexinxiModel'
import util from '../lib/util'
import jwt from 'jsonwebtoken'

export default ({ config, db }) => {
	let api = Router()

	// 分页接口（后端）
	api.get('/page', async (req, res) => {

		try {

			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let xuehao = req.query.xuehao
			if (xuehao) {//学号

				if (xuehao.indexOf('%') != -1) {
					where.xuehao = {
						[Op.like]: xuehao
					}
				} else {
					where.xuehao = {
						[Op.eq]: xuehao
					}
				}
			}
			let xueshengxingming = req.query.xueshengxingming
			if (xueshengxingming) {//学生姓名

				if (xueshengxingming.indexOf('%') != -1) {
					where.xueshengxingming = {
						[Op.like]: xueshengxingming
					}
				} else {
					where.xueshengxingming = {
						[Op.eq]: xueshengxingming
					}
				}
			}
			let touxiang = req.query.touxiang
			if (touxiang) {//图片

				if (touxiang.indexOf('%') != -1) {
					where.touxiang = {
						[Op.like]: touxiang
					}
				} else {
					where.touxiang = {
						[Op.eq]: touxiang
					}
				}
			}
			let xingbie = req.query.xingbie
			if (xingbie) {//性别

				if (xingbie.indexOf('%') != -1) {
					where.xingbie = {
						[Op.like]: xingbie
					}
				} else {
					where.xingbie = {
						[Op.eq]: xingbie
					}
				}
			}
			let shoujihaoma = req.query.shoujihaoma
			if (shoujihaoma) {//手机号码

				if (shoujihaoma.indexOf('%') != -1) {
					where.shoujihaoma = {
						[Op.like]: shoujihaoma
					}
				} else {
					where.shoujihaoma = {
						[Op.eq]: shoujihaoma
					}
				}
			}
			let zhuanye = req.query.zhuanye
			if (zhuanye) {//专业

				if (zhuanye.indexOf('%') != -1) {
					where.zhuanye = {
						[Op.like]: zhuanye
					}
				} else {
					where.zhuanye = {
						[Op.eq]: zhuanye
					}
				}
			}
			let jiuyeqiye = req.query.jiuyeqiye
			if (jiuyeqiye) {//就业企业

				if (jiuyeqiye.indexOf('%') != -1) {
					where.jiuyeqiye = {
						[Op.like]: jiuyeqiye
					}
				} else {
					where.jiuyeqiye = {
						[Op.eq]: jiuyeqiye
					}
				}
			}
			let jiuyegangwei = req.query.jiuyegangwei
			if (jiuyegangwei) {//就业岗位

				if (jiuyegangwei.indexOf('%') != -1) {
					where.jiuyegangwei = {
						[Op.like]: jiuyegangwei
					}
				} else {
					where.jiuyegangwei = {
						[Op.eq]: jiuyegangwei
					}
				}
			}
			let qiyedizhi = req.query.qiyedizhi
			if (qiyedizhi) {//就业地址

				if (qiyedizhi.indexOf('%') != -1) {
					where.qiyedizhi = {
						[Op.like]: qiyedizhi
					}
				} else {
					where.qiyedizhi = {
						[Op.eq]: qiyedizhi
					}
				}
			}
			let jiuyeriqi = req.query.jiuyeriqi
			if (jiuyeriqi) {//就业日期

				if (jiuyeriqi.indexOf('%') != -1) {
					where.jiuyeriqi = {
						[Op.like]: jiuyeriqi
					}
				} else {
					where.jiuyeriqi = {
						[Op.eq]: jiuyeriqi
					}
				}
			}
			let beizhu = req.query.beizhu
			if (beizhu) {//备注
				if (beizhu.indexOf('%') != -1) {
					where.beizhu = {
						[Op.like]: beizhu
					}
				} else {
					where.beizhu = {
						[Op.eq]: beizhu
					}
				}
			}
			let dengjishijian = req.query.dengjishijian
			if (dengjishijian) {//登记时间

				if (dengjishijian.indexOf('%') != -1) {
					where.dengjishijian = {
						[Op.like]: dengjishijian
					}
				} else {
					where.dengjishijian = {
						[Op.eq]: dengjishijian
					}
				}
			}
			let userid = req.query.userid
			if (userid) {

				if (userid.indexOf('%') != -1) {
					where.userid = {
						[Op.like]: userid
					}
				} else {
					where.userid = {
						[Op.eq]: userid
					}
				}
			}
			let tableName = 
			req.session.userinfo === undefined ? 
			jwt.decode(req.headers.token).tableName : //解析token
			req.session.userinfo.tableName
			if(tableName == 'xuesheng') {//学生表
				where.xuehao = {
					[Op.eq]: req.session.userinfo === undefined ? jwt.decode(req.headers.token).username : req.session.userinfo.xuehao
				}
				if (where['userid'] != undefined) {
					delete where.userid
				}
			}

			let result = await JiuyexinxiModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 分页接口（前端）
	api.get('/list', async (req, res) => {

		try {

			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let xueshengxingming = req.query.xueshengxingming
			if (xueshengxingming) {

				if (xueshengxingming.indexOf('%') != -1) {
					where.xueshengxingming = {
						[Op.like]: xueshengxingming
					}
				} else {
					where.xueshengxingming = {
						[Op.eq]: xueshengxingming
					}
				}
			}
			let zhuanye = req.query.zhuanye
			if (zhuanye) {

				if (zhuanye.indexOf('%') != -1) {
					where.zhuanye = {
						[Op.like]: zhuanye
					}
				} else {
					where.zhuanye = {
						[Op.eq]: zhuanye
					}
				}
			}
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where.xuehao = {
					[Op.eq]: req.session.userinfo === undefined ? jwt.decode(req.headers.token).username : req.session.userinfo.xuehao
				}
			}

			let result = await JiuyexinxiModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {	
			toRes.session(res, 401, '您的权限不够！', '', 200)
		}
	})

	// 保存接口（后端）
	api.post('/save', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			if (!req.body.userid) {
				req.body.userid = req.session.userinfo === undefined ? jwt.decode(req.headers.token).id : req.session.userinfo.id
			}

			const userinfo = await JiuyexinxiModel.create(req.body)

			if (userinfo === null) {

				toRes.session(res, -1, '添加失败！')
			} else {

				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 保存接口（前端）
	api.post('/add', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			if (jwt.decode(req.headers.token) == null) {
				toRes.session(res, 401, '请登录后再操作', '', 401)
			}

			req.body.userid = req.session.userinfo === undefined ? jwt.decode(req.headers.token).id : req.session.userinfo.id


			const userinfo = await JiuyexinxiModel.create(req.body)

			if (userinfo === null) {

				toRes.session(res, -1, '添加失败！')
			} else {

				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 更新接口
	api.post('/update', async (req, res) => {

		try {
			await JiuyexinxiModel.update(req.body, {
				where: {
				  id: req.body.id || 0
				}
			})


			toRes.session(res, 0, '编辑成功！')
		} catch(err) {
			
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 删除接口
	api.post('/delete', async (req, res) => {

		try {

			await JiuyexinxiModel.destroy({
				where: {
				  id: {
					[Op.in]: req.body
				  }
				}
			})

			toRes.session(res, 0, '删除成功！')
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 详情接口（后端）
	api.all('/info/:id', async (req, res) => {

		try {

			toRes.record(res, 0, await JiuyexinxiModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 详情接口（前端）
	api.all('/detail/:id', async (req, res) => {

		try {

			toRes.record(res, 0, await JiuyexinxiModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 获取需要提醒的记录数接口
	api.get('/remind/:columnName/:type', async (req, res) => {

		try {

			let sql = 'SELECT 0 AS count'
			
			if (req.params.type == 1) {
				if (req.query.remindstart) sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "'"
				if (req.query.remindend) sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " <= '" + req.query.remindend + "'"

				if (req.query.remindstart && req.query.remindend) {
					sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "' AND " + req.params.columnName + " <= '" + req.query.remindend + "'"
				}
			}

			if (req.params.type == 2) {
				if (req.query.remindstart) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " >= '" + remindStart + "'"
				}
				if (req.query.remindend) {
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " <= '" + remindEnd + "'"
				}

				if (req.query.remindstart && req.query.remindend) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM jiuyexinxi WHERE " + req.params.columnName + " >= '" + remindStart + "' AND " + req.params.columnName + " <= '" + remindEnd + "'"
				}
			}

			const results = await sequelize.query(sql, {
				plain: true,
				raw: true,
				type: QueryTypes.SELECT
			})

			toRes.count(res, 0, results.count)
		} catch(err) {
			
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 分组统计接口
	api.get('/group/:columnName', async (req, res) => {

		try {

			let sql = ""
			let columnName = req.params.columnName
			// let tableName = "jiuyexinxi"
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}
			sql = "SELECT COUNT(*) AS total, " + columnName + " FROM jiuyexinxi " + where + " GROUP BY " + columnName + " LIMIT 10" 
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 统计指定字段
	api.get('/value/:xColumnName/:yColumnName', async (req, res) => {

		try {

			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}

			sql = "SELECT " + xColumnName + ", SUM(" + yColumnName + ") AS total FROM jiuyexinxi " + where + " GROUP BY " + xColumnName + " DESC LIMIT 10"
			
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 按日期统计
	api.get('/value/:xColumnName/:yColumnName/:timeStatType', async (req, res) => {

		try {
			
			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let timeStatType = req.params.timeStatType
			let tableName = "jiuyexinxi"
			let where = " WHERE 1 = 1 "
			if (jwt.decode(req.headers.token).role != '管理员') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}

            if (config.dbConnection.dbtype.toLowerCase() == "mysql") {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m')  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y')  LIMIT 10";
            } else {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120)  LIMIT 10";
            }
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})







	return api
}
